.mainTechno {
  display: flex;
  flex-direction: column;
  width: 70vw;
  margin: 0 auto 70px;
  height: 100vh;
  box-sizing: border-box;
  /*border: 2px solid red;*/
}

.technoTitle {
  width: 30vw;
