    justify-content: center;
  }

  .contatoIcons .icon p {
    font-size: 1.5em;
  }
}

@media screen and (max-width:320px) {
  .mainContato {
    height: 100vh;
  }

  .contatoIcons {
    height: 75vh;
  }

  .icon {
    height: 25vh;
   
  }

  .icon p {
    font-size: 1em;
  }
}


