.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Roboto', sans-serif;
  margin: 0 auto;
  padding: 15px;
  width: 80%;
}

.footer p {
  color: #777;
  margin-bottom: 5px;
}








@media screen and (max-width: 375px) {
